import styled from 'styled-components';

export const Background = styled.div`
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 100vh;
	background-color: #333c3d;

	@media (max-width: 480px) {
		flex-direction: column;
		padding-top: 5rem;
		padding-bottom: 0rem;
		height: 100%;
	}
`;

export const HeroImage = styled.div`
	width: 90rem;
	margin-left: 5rem;
	margin-right: 4rem;

	@media (max-width: 480px) {
		margin-top: 5rem;
		width: 15rem;
		margin: 0;
	}
`;

export const Img = styled.img`
	width: 100%;
	border-radius: 200%;
	/* box-shadow:  5px 5px 15px #a4a4a4, 
             -5px -5px 15px #dedede; */
	box-shadow: 0 10px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 18px 0 rgba(0, 0, 0, 0.19);
`;

export const AboutContainer = styled.div`
	margin-right: 5rem;
	margin-left: 2rem;
	color: #c1c1c1;

	@media (max-width: 480px) {
		margin: 2rem;
	}
`;

export const AboutHeader = styled.h2`
	color: #29d256;
	text-shadow: 2px 2px #292c2a;
	font-size: 4rem;
	margin-bottom: 5rem;

	@media (max-width: 480px) {
		font-size: 3rem;
		margin-bottom: 2.5rem;
		text-align: center;
	}
`;

export const AboutContent = styled.p`
	font-size: 1.5rem;
	margin-bottom: 2rem;
	text-shadow: 2px 2px #292c2a;

	@media (max-width: 480px) {
		font-size: 1rem;
	}
`;

export const IconContainer = styled.p`
	font-size: 1.5rem;
	margin-top: 4rem;
	margin-bottom: 2rem;

	@media (max-width: 480px) {
		font-size: 1rem;
		margin-top: 4rem;
		text-align: center;
	}
`;

export const Icon = styled.img`
	margin-right: 2.5rem;
	width: 4rem;
	heigth: 4rem;
	transition: all 0.2s ease-in;

	&:hover {
		transform: scale(1.2);
	}

	@media (max-width: 480px) {
		margin-right: 1.5rem;
		width: 2.5rem;
		heigth: 2.5rem;
	}
`;

export const IconSoc = styled.img`
	margin-right: 2.5rem;
	width: 3rem;
	heigth: 3rem;
	transition: all 0.2s ease-in;

	&:hover {
		transform: scale(1.2);
	}

	@media (max-width: 480px) {
		margin-right: 1.5rem;
		width: 2rem;
		heigth: 2rem;
	}
`;
