import React, { useState } from 'react';
import { Link } from 'react-scroll';
import Education from './components/Education/Education';
import Experience from './components/Experience/Experience';
import Skills from './components/Skills/Skills';
import Awards from './components/Awards/Awards';
import {
	Background,
	Navigation,
	ResumeContainer,
	NavigationLinkEducation,
	NavigationLinkExperience,
	NavigationLinkSkills,
	NavigationLinkAwards
} from './Style';

const Resume = () => {
	const [education, setEducation] = useState(false);
	const [experience, setExperience] = useState(false);
	const [skills, setSkills] = useState(false);
	const [awards, setAwards] = useState(false);

	const handleSetActive = (to) => {
		if (window.screen.availWidth > 360) {
			const section = to.split('-')[2];
			switch (section) {
				case 'Education':
					setEducation(true);
					setExperience(false);
					setSkills(false);
					setAwards(false);
					break;
				case 'Experience':
					setExperience(true);
					setEducation(false);
					setSkills(false);
					setAwards(false);
					break;
				case 'Skills':
					setSkills(true);
					setEducation(false);
					setExperience(false);
					setAwards(false);
					break;
				case 'Awards':
					setAwards(true);
					setEducation(false);
					setExperience(false);
					setSkills(false);
					break;
				default:
					setEducation(false);
					setExperience(false);
					setSkills(false);
					setAwards(false);
			}
		}
	};

	return (
		<Background name="section-Resume">
			<Navigation>
				<NavigationLinkEducation active={education}>
					<Link
						to="section-Resume-Education"
						smooth={true}
						spy={true}
						offset={-100}
						onSetActive={handleSetActive}
					>
						Education
					</Link>
				</NavigationLinkEducation>
				<NavigationLinkExperience active={experience}>
					<Link
						to="section-Resume-Experience"
						smooth={true}
						spy={true}
						offset={-100}
						onSetActive={handleSetActive}
					>
						Experience
					</Link>
				</NavigationLinkExperience>
				<NavigationLinkSkills active={skills}>
					<Link
						to="section-Resume-Skills"
						smooth={true}
						spy={true}
						offset={-100}
						onSetActive={handleSetActive}
					>
						Skills
					</Link>
				</NavigationLinkSkills>
				<NavigationLinkAwards active={awards}>
					<Link
						to="section-Resume-Awards"
						smooth={true}
						spy={true}
						offset={-100}
						onSetActive={handleSetActive}
					>
						Awards
					</Link>
				</NavigationLinkAwards>
			</Navigation>
			<ResumeContainer>
				<Education />
				<Experience />
				<Skills />
				<Awards />
			</ResumeContainer>
		</Background>
	);
};

export default Resume;
