import React from 'react';
import { Container, BoxContainer, IconContainer, Header, Field } from './Style';
import { HiOutlineMail } from 'react-icons/hi';
import { CgWebsite } from 'react-icons/cg';
import { RiContactsFill } from 'react-icons/ri';
import { FaAddressBook } from 'react-icons/fa';

const Box = (props) => {
	return (
		<Container>
			{props.list &&
				props.list.map((el, i) => {
					return (
						<BoxContainer>
							<IconContainer>
								{el.icon === 'FaAddressBook' && <FaAddressBook />}
								{el.icon === 'RiContactsFill' && <RiContactsFill />}
								{el.icon === 'HiOutlineMail' && <HiOutlineMail />}
								{el.icon === 'CgWebsite' && <CgWebsite />}
							</IconContainer>
							<Header>{el.name}</Header>
							<Field>{el.field}</Field>
						</BoxContainer>
					);
				})}
		</Container>
	);
};

export default Box;
