import React from 'react';
import { CgWebsite } from 'react-icons/cg';
import { FaMedapps } from 'react-icons/fa';
import { FaDatabase } from 'react-icons/fa';
import { Container, Header, BoxContainer, IconContainer } from './Style';

const Box = (props) => {
	return (
		<Container>
			{props.list &&
				props.list.map((el, i) => {
					return (
						<BoxContainer key={i + 1}>
							<IconContainer>
								{el.icon === 'CgWebsite' && <CgWebsite />}
								{el.icon === 'FaMedapps' && <FaMedapps />}
								{el.icon === 'FaDatabase' && <FaDatabase />}
							</IconContainer>
							<Header>{el.name}</Header>
						</BoxContainer>
					);
				})}
		</Container>
	);
};

export default Box;
