import React from 'react';
import mypic2 from './../staticFiles/own.jpeg';
import socurcerer from './../staticFiles/sourcerer.svg';
import linkedin from './../staticFiles/social-2.svg';
import github from './../staticFiles/git_logo.png';
import insta from './../staticFiles/social-4.png';
import { Background, HeroImage, Img, AboutContainer, AboutHeader, AboutContent, Icon, IconContainer , IconSoc} from './style';

const Aboutscreen = () => {
	return (
		<Background name="section-About">
			<HeroImage data-aos="fade-up">
				<Img src={mypic2} alt="gadgetvala" />
			</HeroImage>
			<AboutContainer data-aos="fade-up">
				<AboutHeader>About Me</AboutHeader>
				<AboutContent>
					I am a full-stack Web Developer, currently living in Vadodara, Gujarat. I am pursuing B.Tech in
					Computer Science Engineering from Parul University, and my primary focus and inspiration for my
					studies are Web Development.
				</AboutContent>
				<AboutContent>
					I am both driven and self-motivated, and I am constantly experimenting with new technologies and
					techniques. I am very passionate about Web Development and blockchain enthusiasts, strive to better
					myself as a developer.
				</AboutContent>
				<IconContainer>
					<a href="https://linkedin.com/in/gadgetvala">
						<Icon src={linkedin} alt={'linkedin'} />
					</a>
					<a href="https://github.com/gadgetvala">
						<Icon src={github} alt={'github'} />
					</a>
					<a href="https://sourcerer.io/gadgetvala">
						<IconSoc src={socurcerer} alt={'socurcerer'} />
					</a>
					<a href="https://instagram.com/gadgetvala">
						<Icon src={insta} alt={'insta'} />
					</a>
				</IconContainer>
			</AboutContainer>
		</Background>
	);
};

export default Aboutscreen;
