import React from 'react';
import Box from './components/Box/Box';
import witw from './../staticFiles/Whereintheworld-min.png';
import tmdb from './../staticFiles/tmdbMovies-min.png';
import trillo from './../staticFiles/trillo-min.png';
import vali from './../staticFiles/validator-min.png';
import nato from './../staticFiles/natours-min.png';
import nexter from './../staticFiles/nexter-min.png';
import { Background, Header, Desc, BoxContainer } from './Style';

const Projectscreen = () => {
	return (
		<Background name="section-Project">
			<Header>Project</Header>
			<Desc>Here are some projects I've created or been a part of</Desc>
			<div>
				<BoxContainer>
					<Box src={witw} alt={'witw'} to="https://whereinthecountry.netlify.app" />
					<Box src={tmdb} alt={'tmdb'} to="https://tmdbmovie.herokuapp.com/" />
					<Box src={trillo} alt={'trillo'} to="https://gadgetvala.github.io/Trillo-frontend/" />
				</BoxContainer>
				<BoxContainer>
					<Box src={vali} alt={'vali'} to="https://gadgetvala.github.io/validator/" />
					<Box src={nato} alt={'nato'} to="https://gadgetvala.github.io/Natour-frontend/" />
					<Box src={nexter} alt={'nexter'} to="https://gadgetvala.github.io/Nexter-frontend/" />
				</BoxContainer>
			</div>
			<Desc top>
				<a href="https://github.com/gadgetvala">CLICK HERE</a>
				to Checkout More cool Projects
			</Desc>
		</Background>
	);
};

export default Projectscreen;
