import styled from 'styled-components';

export const Background = styled.div`
	display: flex;
	flex-direction: column;
	height: 100%;
	background-color: #333c3d;
	align-items: center;
	justify-content: space-around;
	padding-top: 3rem;
	padding-bottom: 5rem;

	@media (max-width: 480px) {
		padding: 1rem;
		padding-top: 5rem;
		width: 100%;
	}
`;

export const Header = styled.h3`
	color: #eee;
	font-size: 4rem;
	padding-bottom: 3rem;
	letter-spacing: 5px;
	font-weight: bold;
	text-shadow: 2px 2px #292c2a;

	@media (max-width: 480px) {
		font-size: 3rem;
		text-align: center;
	}
`;

export const Desc = styled.p`
	color: #9c9999;
	font-size: 1rem;
	letter-spacing: 3px;
	padding-bottom: 3rem;
	${(props) => props.top && `padding-top: 3rem;`}

	@media (max-width: 480px) {
		text-align: center;
		${(props) => props.top && `padding-top: 1.5rem;`}
	}
`;

export const BoxContainer = styled.div`
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-around;

	@media (max-width: 480px) {
		flex-direction: column;
	}
`;
