import styled from 'styled-components';

export const Background = styled.div`
	display: flex;
	height: 100%;
	background-color: #333c3d;
	padding-top: 5rem;
	padding-bottom: 5rem;

	@media (max-width: 480px) {
		padding-bottom: 0rem;
		padding-top: 0rem;
		overflow: hidden;
	}
`;

export const Navigation = styled.div`
	position: sticky;
	top: 100px;
	height: 50%;
	overflow: hidden;
	margin-left: 10rem;
	color: #eee;
	padding: 1rem;

	@media (max-width: 480px) {
		display: none;
	}
`;

const navlink = `
	margin-bottom: 1rem;
	font-size: 1.4rem;
	cursor: pointer;
	transition: all 0.02s ease-in;
	`;

const navlinkactive = `
	color: #29d256;
	padding-left: 25px;
	&::before {
		content: '---  ';
		position: realtive;
		top: 50%;
		letter-spacing: -2px;
	}
`;

export const NavigationLink = styled.p`
	${navlink}
`;

export const NavigationLinkEducation = styled.p`
	${navlink}
	${(props) => props.active && navlinkactive}
`;

export const NavigationLinkExperience = styled.p`
	${navlink}
	${(props) => props.active && navlinkactive}
`;

export const NavigationLinkSkills = styled.p`
	${navlink}
	${(props) => props.active && navlinkactive}
`;

export const NavigationLinkAwards = styled.p`
	${navlink}
	${(props) => props.active && navlinkactive}
`;

export const ResumeContainer = styled.div`
	margin-right: 5rem;
	margin-left: 10rem;
	width: 50%;
	padding: 1rem;

	@media (max-width: 480px) {
		margin: 1rem;
		width: 90%;
	}
`;
