import React from 'react';
import { Line } from 'rc-progress';
import {
	Container,
	Header,
	ProgressArea,
	ProgressContainer,
	ProgressName,
	ProgressDetails,
	ProgressPercentage
} from './Style';

const Lineskill = (props) => {
	return (
		<Container>
			<Header>{props.header}</Header>
			<ProgressArea>
				{props.list &&
					props.list.map((el, index) => {
						return (
							<ProgressContainer key={index + 1}>
								<ProgressDetails>
									<ProgressName>{el.name}</ProgressName>
									<ProgressPercentage>{el.progress}%</ProgressPercentage>
								</ProgressDetails>
								<Line
									percent={el.progress}
									strokeWidth="3"
									strokeColor="#29d256"
									trailWidth="3"
									trailColor="#818a9126"
								/>
							</ProgressContainer>
						);
					})}
			</ProgressArea>
		</Container>
	);
};

export default Lineskill;
