import React from 'react';
import { list } from './Data';
import { Header, EducationContainer } from './Style';
import EducationList from './components/EducationList';

const Education = () => {
	return (
		<EducationContainer name="section-Resume-Education">
			<Header data-aos="fade-left">Education</Header>
			<div data-aos="fade-left">
				<EducationList list={list} />
			</div>
		</EducationContainer>
	);
};

export default Education;
