import styled from 'styled-components';

export const BoxContainer = styled.div`
	margin: 1rem;
	width: 25rem;
	height: 12rem;
	border-radius: 10px;
	opacity: 80%;
	overflow: hidden;
	background: #333c3d;
	box-shadow: 8px 8px 17px #2a3132, -8px -8px 17px #3c4748;
	&:hover {
		opacity: 100%;
	}

	@media (max-width: 480px) {
		margin: 0rem;
		margin-bottom: 1.5rem;
		width: 18rem;
		height: 10rem;
	}
`;

export const Img = styled.img`
	border-radius: 10px;
	width: 100%;
	height: 100%;

	transform: scale(1.2);
	transition: all 0.4s ease-in;
	&:hover {
		transform: scale(1);
	}
`;
