import React from 'react';
import { ListContainer, Icon, Time, Item, Description } from './Style';
import { CgWorkAlt } from 'react-icons/cg';

const ExperienceList = (props) => {
	return (
		<>
			{props.list &&
				props.list.map((el,i) => {
					return (
						<ListContainer key={i+1}>
							<Icon>
								<CgWorkAlt />
							</Icon>
							<div>
								<Time>{el.time}</Time>
								<Item>{el.position}</Item>
								<Item>{el.company}</Item>
								<Description>{el.description}</Description>
							</div>
						</ListContainer>
					);
				})}
		</>
	);
};

export default ExperienceList;
