import React from 'react';
import { list } from './Data';
import Box from './components/Box/Box';
import { Background, Header, Desc } from './Style';

const Servicesscreen = () => {
	return (
		<Background name="section-Services">
			<Header>Services</Header>
			<Desc>Here are some of the services I can provide</Desc>
			<div>
				<Box list={list} />
			</div>
		</Background>
	);
};

export default Servicesscreen;
