import React, { useState } from 'react';
import { Link } from 'react-scroll';
import logo from './../staticFiles/logo-white.svg';
import ReactJsTyping from 'reactjs-typing-effect';
import {
	Background,
	Navigation,
	NavigationList,
	NavigationListLinks,
	Img,
	Logo,
	HeroWrapper,
	HeroText,
	HeroTextBox,
	HeroTextBoxHead,
	HeroTextDesign,
	HeroTextBoxSay,
	HamBurger,
	HamBurgerBar,
	HamBurgerMenu
} from './style.js';

const Homescreen = () => {
	const list = [
		'Web Developer',
		'Blockchain Enthusiast',
		'App Developer',
		'AI/ML Enthusiast',
		'UI/UX Designer.',
		'Photographer.'
	];

	const [isOpen, setIsOpen] = useState(true);

	const handleClick = () => {
		setIsOpen(!isOpen);
	};

	return (
		<Background start name="section-Home">
			{window.screen.availWidth <= 480 && !isOpen && (
				<HamBurgerMenu data-aos="flip-right" data-aos-duration="600">
					<NavigationListLinks ms>
						<Link to="section-Home" smooth={true} onClick={handleClick}>
							Home
						</Link>
					</NavigationListLinks>
					<NavigationListLinks ms>
						<Link to="section-About" smooth={true} onClick={handleClick}>
							About
						</Link>
					</NavigationListLinks>
					<NavigationListLinks ms>
						<Link to="section-Resume" smooth={true} onClick={handleClick}>
							Resume
						</Link>
					</NavigationListLinks>
					<NavigationListLinks ms>
						<Link to="section-Services" smooth={true} onClick={handleClick}>
							Services
						</Link>
					</NavigationListLinks>
					<NavigationListLinks ms>
						<Link to="section-Project" smooth={true} onClick={handleClick}>
							Project
						</Link>
					</NavigationListLinks>
					<NavigationListLinks ms>
						<Link to="section-Contact" smooth={true} onClick={handleClick}>
							Contact
						</Link>
					</NavigationListLinks>
				</HamBurgerMenu>
			)}
			<Navigation>
				<Logo>
					<Img src={logo} alt="logo" />
				</Logo>
				{window.screen.availWidth <= 480 && (
					<HamBurger onClick={handleClick}>
						<HamBurgerBar />
						<HamBurgerBar />
						<HamBurgerBar />
					</HamBurger>
				)}
				<NavigationList>
					<li>
						<NavigationListLinks>
							<Link to="section-Home" smooth={true}>
								Home
							</Link>
						</NavigationListLinks>
						<NavigationListLinks>
							<Link to="section-About" smooth={true}>
								About
							</Link>
						</NavigationListLinks>
						<NavigationListLinks>
							<Link to="section-Resume" smooth={true}>
								Resume
							</Link>
						</NavigationListLinks>
						<NavigationListLinks>
							<Link to="section-Services" smooth={true}>
								Services
							</Link>
						</NavigationListLinks>
						<NavigationListLinks>
							<Link to="section-Project" smooth={true}>
								Project
							</Link>
						</NavigationListLinks>
						<NavigationListLinks>
							<Link to="section-Contact" smooth={true}>
								Contact
							</Link>
						</NavigationListLinks>
					</li>
				</NavigationList>
			</Navigation>
			<HeroWrapper>
				<HeroTextBoxSay>HEY! I AM</HeroTextBoxSay>
				<HeroText>Suraj Verma</HeroText>
				<HeroTextBox>
					<HeroTextBoxHead>I`m a </HeroTextBoxHead>
					<HeroTextDesign>
						<ReactJsTyping StringList={list} speed={1200} />
					</HeroTextDesign>
				</HeroTextBox>
			</HeroWrapper>
		</Background>
	);
};

export default Homescreen;
