import React from 'react';
import { list } from './Data';
import Box from './components/Box/Box';
import { Background, Header, Desc } from './Style';

const Contactscreen = () => {
	return (
		<Background name="section-Contact">
			<Header>Contact</Header>
			<Desc>Here's all you need to get in touch with me</Desc>
			<Box list={list} />
		</Background>
	);
};

export default Contactscreen;
