import React from 'react';
import { Header, LineList, Container } from './Style';
import Lineskill from './components/Lineskill/Lineskill';
import { squareSkillList, AppDevelopment, WebDevelopment, Database } from './Data';
import Squareskill from './components/Squareskill/Squareskill';

const Skills = () => {
	return (
		<Container name="section-Resume-Skills">
			<Header data-aos="fade-left">Skills</Header>
			<div>
				<div data-aos="fade-left">
					<Squareskill list={squareSkillList} />
				</div>

				<LineList data-aos="fade-left">
					<Lineskill list={AppDevelopment} header={'App Development'} />
					<Lineskill list={WebDevelopment} header={'Web Development'} />
					<Lineskill list={Database} header={'Database'} />
				</LineList>
			</div>
		</Container>
	);
};

export default Skills;
