import React from 'react';
import { ListContainer, Icon, Time, Degree, Place } from './Style';
import { CgAwards } from 'react-icons/cg';

const Awardlist = (props) => {
	return (
		<>
			{props.list &&
				props.list.map((el, i) => {
					return (
						<ListContainer key={i + 1}>
							<Icon>
								<CgAwards />
							</Icon>
							<div>
								<Time>{el.time}</Time>
								<Degree>{el.name}</Degree>
								<Place>{el.desc}</Place>
							</div>
						</ListContainer>
					);
				})}
		</>
	);
};

export default Awardlist;
