
import styled from 'styled-components';

export const Container = styled.div`
	display: flex;
	flex-direction: row;

	@media (max-width: 480px) {
		flex-direction: column;
	}
`;

export const Header = styled.p`
	font-size: 1.4rem;
	font-weight: bold;
	padding-bottom: 0.6rem;
`;

export const BoxContainer = styled.div`
	margin: 1rem;
	padding: 2rem;
	width: 25rem;
	height: 12rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	border-radius: 10px;
	background: #333c3d;
	box-shadow: 8px 8px 17px #2a3132, -8px -8px 17px #3c4748;
	color: #4acd6d;
	/* &:hover {
		background: #45666a;
		color: #eee;
		box-shadow: 8px 8px 17px #3f5d60, -8px -8px 17px #4b6f74;
	} */

	&:hover {
		color: #eee;
		background: #4acd6d;
		/* box-shadow: 8px 8px 17px #45bf65, -8px -8px 17px #4fdb75; */
	}

	@media (max-width: 480px) {
		width: 18rem;
		height: 10rem;
	}
`;

export const IconContainer = styled.div`
	font-size: 4rem;
`;
