import styled from 'styled-components';

export const Background = styled.div`
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #505f60;
	padding: 1.2rem;
	font-size: 1.2rem;
	color: #eee;
	letter-spacing: 1rem;
	text-transform: uppercase;

	@media (max-width: 480px) {
		font-size: 1rem;
		padding: 1rem;
		letter-spacing: .2rem;
	}
`;

export const Heart = styled.span`
	color: #ee2f0f;
	font-size: 1.5rem;
	text-shadow: 0;
`;
