import styled from 'styled-components';

export const Container = styled.div`
	padding-top: 3rem;
	display: flex;
	align-items: center;
	justify-content: space-around;

	@media (max-width: 480px) {
		flex-direction: column;
	}
`;

export const Box = styled.div`
	margin-left: 4rem;
	padding: 0.2rem;
	border-radius: 10px;
	background: #333c3d;
	box-shadow: 8px 8px 17px #2a3132, -8px -8px 17px #3c4748;
	width: 13rem;
	height: 13rem;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-around;

	@media (max-width: 480px) {
		margin: 1rem 1rem 1.5rem 1rem;
	}
`;

export const BoxName = styled.p`
	color: #eee;
	font-size: 1.3rem;
	padding-top: 0.5rem;
	padding-right: 0.5rem;
	padding-left: 0.5rem;
	text-align: center;
`;
