import React from 'react';
import ProgressArc from 'progress-arc-component';
import { Container, Box, BoxName } from './style';

const Squareskill = (props) => {
	return (
		<Container>
			{props.list &&
				props.list.map((el, index) => {
					return (
						<Box key={index + 1}>
							<BoxName>{el.name}</BoxName>
							<ProgressArc
								value={parseInt(el.progress)}
								arcColor="#29d256"
								arcBackgroundColor="#818a9126"
								textColor="#eee"
								rounded={true}
							/>
						</Box>
					);
				})}
		</Container>
	);
};

export default Squareskill;
